import React from "react";
import ReactDOM from "react-dom";
import MapExample from "./components/MapExample";
import MapInteractive from "./components/MapInteractive";
import "./styles.css";
import Header from "./components/Header";
import DataKey from "./components/DataKey";
import Button3D2D from "./components/Button3D2D";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activated: 1 };
  }
  changeFlag(x) {
    this.setState({ activated: x });
  }
  render() {
    return (
      <div className="App">
        <Header />
        <div className="infoKey">
          <div className="dataKey">
            <DataKey />
          </div>
          <div className="mapClass">
            <div className="mapContainer">
              <MapExample />
            </div>
            <div className="button3d2d">
              <div className="buttonStyle" onClick={() => this.changeFlag(1)}>
                <Button3D2D
                  name="3D"
                  isActive={this.state.activated == 1 ? "true" : "false"}
                />
              </div>
              <div className="buttonStyle" onClick={() => this.changeFlag(0)}>
                <Button3D2D
                  name="2D"
                  isActive={this.state.activated == 0 ? "true" : "false"}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
export default App;
