import React, { Component } from "react";
import { render } from "react-dom";
import { StaticMap } from "react-map-gl";
import DeckGL, { GeoJsonLayer } from "deck.gl";
import { scaleThreshold } from "d3-scale";

// Set your mapbox token here
const MAPBOX_TOKEN =
  "pk.eyJ1Ijoic21peWFrYXdhIiwiYSI6ImNqbDVncmNjYjBoMjQzbG50bnVxNWk2YW8ifQ.uy0MalaDN4I5MxPYT99hMA"; //process.env.MapboxAccessToken; // eslint-disable-line

// Source data GeoJSON
const DATA_URL =
  "https://uploads.codesandbox.io/uploads/user/7a90ac77-2767-4d60-a126-90a9cef86b2f/APyT-Edificios_GJSON.geojson"; // eslint-disable-line

export const COLOR_SCALE = scaleThreshold()
  .domain([
    -0.6,
    -0.45,
    -0.3,
    -0.15,
    0,
    0.15,
    0.3,
    0.45,
    0.6,
    0.75,
    0.9,
    1.05,
    1.2
  ])
  .range([
    [65, 182, 196],
    [127, 205, 187],
    [199, 233, 180],
    [237, 248, 177],
    // zero
    [255, 255, 204],
    [255, 237, 160],
    [254, 217, 118],
    [254, 178, 76],
    [253, 141, 60],
    [252, 78, 42],
    [227, 26, 28],
    [189, 0, 38],
    [128, 0, 38]
  ]);

const LIGHT_SETTINGS = {
  lightsPosition: [-125, 50.5, 5000, -122.8, 48.5, 8000],
  ambientRatio: 0.2,
  diffuseRatio: 0.5,
  specularRatio: 0.3,
  lightsStrength: [2.0, 0.0, 1.0, 0.0],
  numberOfLights: 2
};

export const INITIAL_VIEW_STATE = {
  // width: '100%',
  // height: '100%',
  latitude: -33.4330113,
  longitude: -70.613515,
  zoom: 15,
  maxZoom: 16,
  pitch: 55,
  bearing: 0
};

class MapInteractive extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      hoveredObject: null
    };
    this._onHover = this._onHover.bind(this);
    this._renderTooltip = this._renderTooltip.bind(this);
  }

  state = {
    viewport: {
      latitude: -33.4330113,
      longitude: -70.613515,
      zoom: 15,
      maxZoom: 16,
      pitch: 55,
      bearing: 0
    }
  };

  componentDidMount() {
    window.addEventListener("resize", this._resize.bind(this));
    this._resize();
  }

  _resize() {
    this._onViewportChange({
      width: "100%",
      height: "100%"
    });
  }

  _onViewportChange = viewport => {
    this.setState({
      viewport: { ...this.state.viewport, ...viewport }
    });
    console.log(this.state.activation);
  };

  _onHover({ x, y, object }) {
    this.setState({ x, y, hoveredObject: object });
  }

  _renderLayers() {
    const { data = DATA_URL } = this.props;

    return [
      new GeoJsonLayer({
        id: "geojson",
        data,
        opacity: 0.8,
        stroked: false,
        filled: true,
        extruded: true,
        wireframe: true,
        fp64: true,
        getElevation: f => 1000,
        getFillColor: f => COLOR_SCALE(f.properties.PISOS),
        getLineColor: [255, 255, 255],
        lightSettings: LIGHT_SETTINGS,
        pickable: true,
        onHover: this._onHover
      })
    ];
  }

  _renderTooltip() {
    const { x, y, hoveredObject } = this.state;
    return (
      hoveredObject && (
        <div className="tooltip" style={{ top: y, left: x }}>
          <div>
            <b>Average Property Value</b>
          </div>
          <div>
            <div>${hoveredObject.properties.valuePerParcel} / parcel</div>
            <div>
              ${hoveredObject.properties.valuePerSqm} / m<sup>2</sup>
            </div>
          </div>
          <div>
            <b>Growth</b>
          </div>
          <div>{Math.round(hoveredObject.properties.growth * 100)}%</div>
        </div>
      )
    );
  }

  render() {
    const { viewState, controller = true, baseMap = true } = this.props;
    var GeoLayers = this._renderLayers();
    console.log(GeoLayers);
    return (
      <DeckGL
        className="mycontainer"
        container="container"
        layers={GeoLayers}
        initialViewState={INITIAL_VIEW_STATE}
        viewState={viewState}
        controller={controller}
        onViewportChange={this._onViewportChange}
        width="50%"
        height="50%"
      >
        {baseMap && (
          <StaticMap
            reuseMaps
            mapStyle="mapbox://styles/mapbox/light-v9"
            preventStyleDiffing={true}
            mapboxApiAccessToken={MAPBOX_TOKEN}
          />
        )}
      </DeckGL>
    );
  }
}

// { this._renderTooltip }

export default MapInteractive;
