import React from "react";

class Button3D2D extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activation: 0 };
  }

  tick() {
    const x = this.state.activation;
    this.setState({
      activation: !x
    });
  }
  activateLasers(x) {
    console.log(x);
    // activation = x == 1 ? 1 : 0;
    console.log(this.state.activation);
    this.setState({ activation: !this.state.activation });
  }
  render() {
    var activated =
      (this.props.isActive == "true" ? 1 : 0) != this.state.activation;
    let className = activated ? "activeButtonStyle" : "inactiveButtonStyle";
    return (
      <div className={className}>
        <span>
          <h6>{this.props.name}</h6>
        </span>
      </div>
    );
  }
}

export default Button3D2D;
