import React from "react";
import DeckGL, { LineLayer, MeshLayer } from "deck.gl";
import MapGL from "react-map-gl";

const MAPBOX_TOKEN =
  "pk.eyJ1Ijoic21peWFrYXdhIiwiYSI6ImNqbDVncmNjYjBoMjQzbG50bnVxNWk2YW8ifQ.uy0MalaDN4I5MxPYT99hMA";

const data = [
  {
    sourcePosition: [-122.41669, 37.7853],
    targetPosition: [-122.41669, 37.781]
  }
];

class MapExample extends React.Component {
  state = {
    viewport: {
      width: 500,
      height: 500,
      latitude: -33.4330113,
      longitude: -70.613515,
      zoom: 15
    }
  };

  componentDidMount() {
    window.addEventListener("resize", this._resize.bind(this));
    this._resize();
  }

  _resize() {
    this._onViewportChange({
      width: "100%",
      height: "100%"
    });
  }

  _onViewportChange = viewport => {
    this.setState({
      viewport: { ...this.state.viewport, ...viewport }
    });
    console.log(window.activation);
  };

  render() {
    const layers = [new LineLayer({ id: "line-layer", data })];

    return (
      <MapGL
        {...this.state.viewport}
        onViewportChange={this._onViewportChange}
        mapboxApiAccessToken={MAPBOX_TOKEN}
      />
    );
  }
}
// <DeckGL {...this.state.viewport} layers={layers} />

export default MapExample;
